import { useState } from "react";
import { ROUND_TABLE_AVAILABLE, ROUND_TABLE_SELECTED, ROUND_TABLE_SOLED, RECTANGLE_TABLE_AVAILABLE, RECTANGLE_TABLE_SELECTED, RECTANGLE_TABLE_SOLED } from "./TableMapComponents";
import { Modal, Menu, Dropdown } from 'antd';
import { EditOutlined, EyeOutlined } from "@ant-design/icons";
import { toggleViewModel } from "../../store/models-slice";
import { useDispatch } from "react-redux";

const { confirm } = Modal;



export const RoundTable = ({ table_status }) => {

    const dispatch = useDispatch()

    let handleView = () => {

        dispatch(toggleViewModel())

    }


    const menu = (
        <Menu className='p-1'>
    
            <Menu.Item key="1" onClick={handleView}  >
                <div className="d-flex flex-row align-items-center justify-content-left pr-5 mt-1 mb-1">
                    <EyeOutlined /> <span className=' ml-2'>View</span>
                </div>
            </Menu.Item>
    
            <Menu.Item key="2" >
                <div className="d-flex flex-row align-items-center justify-content-left pr-5 mt-1 mb-1">
                    <EditOutlined /> <span className=' ml-2'>Edit </span>
                </div>
            </Menu.Item>
    
        </Menu>
    );
    

  
    
    



    let [isSelected, setSelected] = useState(false)

    let handleEvent = () => {

        if (table_status) {
            setSelected(!isSelected)
        }
        else {
            console.log("Already Booked");
            showPromiseConfirm()
        }

    }


    return (
        <Dropdown overlay={menu} trigger={['contextMenu']}>
            <div style={{ cursor: "pointer", width: "100%" }} className="d-flex flex-row justify-content-center" onClick={handleEvent}>
                {!table_status ? <ROUND_TABLE_SOLED /> : isSelected ? <ROUND_TABLE_SELECTED /> : <ROUND_TABLE_AVAILABLE />}
            </div>
        </Dropdown>
    )



}


export const RectangleTable = ({ table_status }) => {

    let [isSelected, setSelected] = useState(false)

    let handleEvent = () => {

        if (table_status) {
            setSelected(!isSelected)
        }
        else {
            console.log("Already Booked");
            showPromiseConfirm()
        }

    }


    return (
        <div style={{ cursor: "pointer", width: "fit-content" }} className="d-flex flex-row justify-content-center" onClick={handleEvent}>
            {!table_status ? <RECTANGLE_TABLE_SOLED /> : isSelected ? <RECTANGLE_TABLE_SELECTED /> : <RECTANGLE_TABLE_AVAILABLE />}
        </div>
    )



}


function showPromiseConfirm() {
    Modal.warning({
        title: 'Already Booked',
        content: 'The table you have selected is already booked by another customer.',

    });
}

