export const READ_ORDERS_FROM_API = async (recordCount,eventId) => {

   

   if(eventId!=null)
   {
    let result = null
    let url = `https://api.popup.lol/eventMenu/getAllEventOrders`
    let method = "POST"
    let body = {
        event_id: eventId, //dummy event id 254
        record_count: recordCount,
    }
    let headers = { 'Accept': '*/*' }


    await axios.post(url, body, headers)
        .then(function (response) {
            console.log("API Response...==>",response);
            result = {
                "data": response.data,
                "totalRecords":response.data.total_records
            }
        })



    return result

   }
   else{
       return -1
   }


}

export let convertStampDate = (unixtimestamp) => {


    // Months array
    var months_arr = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    let date = new Date(unixtimestamp);
    let year = date.getFullYear();
    let month = date.getMonth() + 1;
    let dt = date.getDate();

    if (dt < 10) {
        dt = '0' + dt;
    }
    if (month < 10) {
        month = '0' + month;
    }

    let final_date = dt + " " + months_arr[parseInt(month) - 1] + " " + year
    //console.log(year + '-' + months_arr[8] + '-' + dt);
    return final_date
}


export let getActualOrderStatus = (para) => {

    if (para == "Rejected") {
        return { name: "Rejected", value: 0 }
    }
    else if (para == "Placed") {
        return { name: "Placed", value: 1 }
    }
    else if (para == "Ready") {
        return { name: "Ready", value: 1 }
    }

    else if (para == "Preparing") {
        return { name: "Preparing", value: 1 }
    }
    else if (para == "Served") {
        return { name: "Served", value: 2 }
    }


}


export const READ_ORDER_DETAILS_FROM_API = async (para) => {


    let values = {}

    let url = 'https://api.popup.lol/eventMenu/fetchOrderDetailsCms'
    let body = {
        id: para,
    }

    let headers = { 'Accept': '*/*', "Content-Type": "application/json" }

    try {

        await axios.post(url, body, headers)
            .then(function (response) {
                //console.log(response.status, response.data);
                values = { status: response.status, data: response.data.data }

            })
    } catch (error) {

        console.log(error);

    }

    return values


}







