import { createSlice, current } from '@reduxjs/toolkit'


export const selectedEventSlice = createSlice({
  name: 'selectedevent',
  initialState: {
    selectedEvent: null,
  },
  reducers: {
    
    setSelectedEventRedux(state,action){
      action.payload.status?
      state.selectedEvent=action.payload.id
      :state.selectedEvent=null
    }
  }
});

// Action creators are generated for each case reducer function
export const { setSelectedEventRedux} = selectedEventSlice.actions
export default selectedEventSlice.reducer