import React, { useState, useEffect } from 'react';
import { Steps, Button, message, Modal } from 'antd';
import Section3 from './Sections/Section3';
import { useDispatch } from 'react-redux';
import { updateLayoutRedux } from '../../../../store/tabledata-slice';


const { Step } = Steps;



function AddTableModel({ state, setState }) {

    const [current, setCurrent] = React.useState(0);
    let [selectedPosition, setSelectedPosition] = useState(null)

    let dispatch = useDispatch()

    const steps = [
        {
            title: 'Position',
            content: <Section3 state={state} positionState={selectedPosition} setPositionState={setSelectedPosition} />,
        },
        {
            title: 'Second',
            content: '',
        },
        {
            title: 'Last',
            content: ''
        },
    ];


    useEffect(() => {

        setCurrent(0)
        setSelectedPosition(null)

    }, [state])

    //Model open close functions ........................................

    const showModal = () => {
        setState(true);
    };

    const handleOk = () => {

        if (current < 2) {
            msg("Complete the 3 steps to add table")
        } else if(selectedPosition==null){

            msg("Please select position to continue")

        }
        
        else {
            dispatch(updateLayoutRedux(selectedPosition))
            setState(false);
        }



    };

    const handleCancel = () => {
        setState(false);
    };


    //Steps Functions.....................................................
    const next = () => {
        setCurrent(current + 1);
    };

    const prev = () => {
        setCurrent(current - 1);
    };

    const msg = (text) => {
        message.warning({
            content: text,
            duration:.8,
            maxCount:3,
            rtl:true,
            
        });
    };


    return (
        <Modal title="ADD NEW TABLE" bodyStyle={{overflowX: 'auto'}} visible={state} onOk={handleOk} onCancel={handleCancel} width={1500} style={{ minHeight: 500 }}>
            
            <Steps size="small" className="w-100" bodyStyle={{width:"100%"}}  current={current}>
                    {steps.map(item => (
                        <Step key={item.title} title={item.title} />
                    ))}
                </Steps>


            <div className='d-flex flex-column p-0 no-scrollbar' style={{objectFit:"contain", overflow:"scroll"}}>

                <div className="steps-content overflow-scroll w-100 no-scrollbar" style={{width:"100%"}}>
                    {

                        steps[current].content

                    }

                </div>
                
            </div>

            <div className="w-100 d-flex flex-row justify-content-end mt-2" style={{width:"100%"}}>

                {current > 0 && (
                        <Button style={{ margin: '0 8px' }} onClick={() => prev()}>
                            Previous
                        </Button>
                    )}


                    {current < steps.length - 1 && (
                        <Button type="primary" onClick={() => next()}>
                            Next
                        </Button>
                    )}

                    {current === steps.length - 1 && (
                        <Button type="primary" disabled>
                            Next
                        </Button>
                    )}

                </div>

        </Modal>
    )
}

export default AddTableModel
