import { createSlice, current } from '@reduxjs/toolkit'


export const tabledataSlice = createSlice({
  name: 'loader',
  initialState: {
    viewModel: false,
    orderModel:false,
    orderModelData:{}
  },
  reducers: {
    toggleViewModel(state) {
      state.viewModel = !state.viewModel
    },
    toggleOrderModel(state) {
      state.orderModel = !state.orderModel
    },
    setOrderModelData(state,action){
      state.orderModelData=action.payload
    }
  }
});

// Action creators are generated for each case reducer function
export const { toggleViewModel,toggleOrderModel,setOrderModelData } = tabledataSlice.actions
export default tabledataSlice.reducer