import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import '../../../../../css/tablemap.css'
import TableCell from './TableCell'

function Section3({ state, positionState, setPositionState }) {

    let reduxdata = useSelector((state) => state.layout.layout)
    let [layoutdata, setLayoutData] = useState(reduxdata)

    //console.log("selected=>",positionState);


    useEffect(() => {

        setLayoutData(reduxdata)

    }, [reduxdata])


    let handletdClick = (row, column, isBooked) => {

        isBooked ?
            console.log("Already Booked")
            : console.log(null);

    }


    return (
        <div className='d-flex flex-column w-100 '>

            <div className='d-flex flex-row overflow-scroll w-100'>
                <table className="mt-3 w-100" style={{
                maxWidth:"100%"
            }}>
                    <tbody>

                        {
                            layoutdata.length >= 1 ?

                                layoutdata.map((obj, key) => {

                                    return (
                                        <tr>
                                            {
                                                obj.map((obj2, key2) => {

                                                    return (

                                                        <>

                                                            <TableCell obj={obj2} row={key} state={positionState} setState={setPositionState} />
                                                        </>
                                                    )

                                                })
                                            }


                                        </tr>
                                    )

                                })
                                : <span className="text-danger">Please create event layout first then create tables.</span>

                        }

                    </tbody>
                </table>
            </div>



        </div>
    )
}

export default Section3
