import {
  Row,
  Col,
  Card,
  Table,
  Badge,
  Dropdown,
  ProgressBar,
  OverlayTrigger,
  Tooltip,
} from "react-bootstrap";
import { Modal, Pagination } from 'antd';


import React, { useEffect, useState } from "react";
import { SearchIcon } from "../icons/SVGIcons.js/OrderIcons";
import samplepdf from '../Assets/PDF/sample.pdf'
import { api } from "../apis/apis";
import axios from "axios";
import { printPfd } from "../helper/Helper";
import { convertStampDate, getActualOrderStatus, READ_ORDERS_FROM_API, READ_ORDER_DETAILS_FROM_API } from "../helper/OrdersHelper";
import OrderTableActionButtons from "../components/Orders Components/OrderTableActionButtons";
import { useDispatch, useSelector } from "react-redux";
import { setOrderModelData, toggleOrderModel } from "../store/models-slice";
import useHttp from "../hooks/use-http";
import { POST_METHOD } from "../apis/apiHelper";
import ViewOrderModal from "../components/TableMapComponents/View Order Model/ViewOrderModal";


const Orders = () => {

  let reduxVIew = useSelector((state) => state.model.orderModel)
  let reduxSelectedEvent = useSelector((state) => state.selectedEvent.selectedEvent)
  //console.log(reduxVIew);
  let dispatch = useDispatch()
  const { sendRequest: FetchHostedEvents } = useHttp();

  let [tableData, setTabledata] = useState(null)
  let [orderData, setOrderData] = useState(null)
  let [totalPage, setTotalPage] = useState(0)
  let [activePage, setActivePage] = useState(0)

 

  useEffect(() => {

    READ_ORDERS_FROM_API(activePage, reduxSelectedEvent).then((res) => {
      console.log("data Getting", res);

      if (res != -1) {
        setTotalPage(res.totalRecords)
        setTabledata(res.data.data)
        
      }
      else{
        console.log("Please select the event in select popup page");
      }
    })


  }, [])


  //only for ok cancel click
  let handleClick = () => {

    dispatch(toggleOrderModel())

  }


  //only for table row click
  let handleTrClick = (para) => {


    READ_ORDER_DETAILS_FROM_API(para).then((res) => {

      //console.log(res.status);

      if (res.status == "200") {
        setOrderData(res.data)
        dispatch(toggleOrderModel())

      }
      else {

        console.log("No data from API");
      }

    })


  }

  function onPageChange(pageNumber) {
    setActivePage(pageNumber - 1)
  }

  useEffect(() => {

    READ_ORDERS_FROM_API(activePage).then((res) => {
      //console.log("data Getting",res);
      setTotalPage(res.totalRecords)
      setTabledata(res.data)
    })


  }, [activePage])

  const svg1 = (
    <svg width="20px" height="20px" viewBox="0 0 24 24" version="1.1">
      <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
        <rect x="0" y="0" width="24" height="24"></rect>
        <circle fill="#000000" cx="5" cy="12" r="2"></circle>
        <circle fill="#000000" cx="12" cy="12" r="2"></circle>
        <circle fill="#000000" cx="19" cy="12" r="2"></circle>
      </g>
    </svg>
  );

  return (
    <Col xl={14}>

      <div className="row">

        <div className="order-search-container">
          <input style={{ border: 0, outline: 0 }} type="text" name="" id="" />
          <SearchIcon />
        </div>

      </div>

      <Card className="p-0">
        <Card.Body className="p-0">

          <Modal
            title="Order Details"
            centered
            visible={reduxVIew}
            onOk={() => handleClick()}
            onCancel={() => handleClick()}
            width={1500}
            footer={[]}
            className="test-model"
          >
            <ViewOrderModal data={orderData} />
          </Modal>


          <Table responsive hover >
            <thead>
              <tr>
                <th className="width80">
                  <strong>#</strong>
                </th>
                <th>
                  <strong>ID</strong>
                </th>
                <th>
                  <strong>DATE</strong>
                </th>
                <th>
                  <strong>PAYMENT METHOD</strong>
                </th>
                <th>
                  <strong>STATUS</strong>
                </th>
                <th>
                  <strong>PRICE</strong>
                </th>
                <th className="text-center">
                  <strong>ORDER ACTION</strong>
                </th>

              </tr>
            </thead>
            <tbody>

              {
                tableData!=null ?

                  tableData.map((obj, key) => {

                    
                    let status = getActualOrderStatus(obj.status)
                    console.log(status);

                    return (

                      <tr>
                        <td onClick={() => { handleTrClick(obj.id) }}>
                          <strong>{key + 1}</strong>
                        </td>
                        <td style={{ cursor: "pointer" }} onClick={() => { handleTrClick(obj.id) }}>{obj.id}</td>
                        <td style={{ cursor: "pointer" }} onClick={() => { handleTrClick(obj.id) }}>{convertStampDate(obj.created_date)}</td>
                        <td style={{ cursor: "pointer" }} onClick={() => { handleTrClick(obj.id) }}>{obj.payment_method}</td>
                        <td style={{ cursor: "pointer" }} onClick={() => { handleTrClick(obj.id) }}>
                          <Badge variant={status.value == 0 ? "danger light" : status.value == 1 ? "warning light" : status.value == 2 ? "success light" : null}>{status.name}</Badge> 
                        </td>
                        <td onClick={() => { handleTrClick("1") }}>${obj.price}</td>
                        <td className=" p-0 text-center ">


                         <OrderTableActionButtons orderStatus={status.value} orderID={obj.id} />

                        </td>
                      </tr>

                    )
                  })

                  : console.log("no orders available")
              }




            </tbody>
          </Table>

          <div className={totalPage > 10 ? "d-flex justify-content-end flex-row" : "d-none"}>

            <Pagination className="pagination-main" defaultCurrent={activePage + 1} total={totalPage} onChange={onPageChange} style={{ margin: "25px 30px", width: "max-content" }} />

          </div>


        </Card.Body>
      </Card>






    </Col>
  );
};

export default Orders;
